document.addEventListener("DOMContentLoaded", function () {
  var mySwiper = new Swiper(".article-swiper", {
    slidesPerView: 3,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    breakpoints: {
      0: {
        enabled: true,
        slidesPerView: 2,
        spaceBetween: 10,
      },
      601: {
        enabled: true,
        slidesPerView: 3,
        direction: 'horizontal',
        spaceBetween: 10,
      },
      961: {
        direction: 'vertical',
        slidesPerView: 3,
        spaceBetween: 60,
      },
    },
  });
});